import React, { useEffect, useState } from "react";
import jsonData from "../../../json/data.json";
import { ScrollParallax } from "react-just-parallax";

const Gallery = () => {
  const [galState, setgalState] = useState({
    incriment: 0,
    reset: false,
  });
  const updateGalleryIndex = () => {
    if (galState.reset) {
      setgalState((pre) => ({ ...pre, reset: false }));
    }
    let currentIndex = galState.incriment;
    currentIndex < jsonData.work_history.length - 1
      ? setgalState((pre) => ({ ...pre, incriment: pre.incriment + 1 }))
      : setgalState((pre) => ({ ...pre, incriment: 0 }));
  };

  useEffect(() => {
    let loaded = true,
      int;
    if (loaded) {
      int = setInterval(() => updateGalleryIndex(), 10000);
    }
    return () => {
      loaded = false;
      clearInterval(int);
    };
  });
  return (
    <div className="inline-flex justify-center items-center relative mt-0 sm:mt-60">
      <ScrollParallax zIndex={1}>
        <ul className="inline-flex flex-col backdrop-blur-md text-white bg-gradB/25 rounded-xl">
          {jsonData.work_history.map(
            ({ company, title, duration }, index) => (
              <li
                className={` bottom-0 transition-all w-full  p-4 z-0 `}
                key={index}
              >
                <p className="flex w-full h-full">
                  <span className="flex flex-col gap-2 w-full flex-1 h-full">
                    <span className="text-xl">{company}</span>
                    <span className="text-lg">{title}</span>
                    <span className="text-sm">{duration}</span>
                  </span>
                </p>
              </li>
            )
          )}
        </ul>
      </ScrollParallax>
    </div>
  );
};

export default Gallery;
